//
import H5Calendar from "./calendar.vue";

//  install  Vue  use 
const install = function(Vue) {
  // 
  if (install.installed) return;

  // 
  Vue.component("H5Calendar", H5Calendar);

  // 
  if (typeof window != "undefined" && window.Vue) {
    install(window.Vue);
  }
};

export default {
  //  install Vue.use() 
  install,
  // 
  H5Calendar,
};
